    width: 40.5%
  }
  .col-sm-6 {
    width: 49%
  }
  .col-sm-7 {
    width: 57.5%
  }
  .col-sm-8 {
    width: 66%
  }
  .col-sm-9 {
    width: 74.5%
  }
  .col-sm-10 {
    width: 83%
  }
